@media all and (max-width: 991px) {
	.fixed_header {
		.header_info {
			display: none; }

		.aside_category {
			.aside_category_child_list {
				position: relative;
				left: auto;
				right: auto;
				z-index: 25; } } }

	.header {
		&_menu {
			.container {
				height: 37px;
				position: relative; }
			&_list {
				display: none;
				margin: 0;
				position: absolute;
				z-index: 20;
				width: 150px;
				top: 37px;
				right: 10px;
				&_item {
					width: 100%;
					display: block;
					@include linear-gradient(to top, #7fe0f8 0%, #cef8ff 100%);
					border-bottom: 1px solid $blue;
					&:last-child {
						border-bottom: none; }
					&:before,
					&:after {
						display: none; } } }
			&_button {
				display: block;
				position: absolute;
				right: 10px;
				top: 9px;
				@include cursor;
				&:hover {
					div {
						@include box-shadow(0 0 3px 1px $white); } }
				div {
					width: 30px;
					height: 4px;
					margin-bottom: 4px;
					background: $blue;
					@include transition(0.25s);
					&:last-child {
						margin-bottom: 0; } } } } }
	.footer_contacts {
		margin-left: -20px;
		&:before {
			display: none; } } }



